import { parseHTML } from "../src/parser.js";
import test from "ava";

test("parse a single element", (t) => {
  let doc = parseHTML("<div></div>");
  let div = doc.children[0];
  if (div.tagName == "div") {
    t.pass();
  }

  if (div.children.length === 0) {
    t.pass();
  }

  if (div.type === "element") {
    t.pass();
  }

  if (div.attributes.length === 2) {
    t.pass();
  }
});

test("parse a single element with text content", (t) => {
  let doc = parseHTML("<div>hello</div>");
  let text = doc.children[0].children[0];
  if (text.content === "hello") {
    t.pass();
  }
  if (text.type === "text") {
    t.pass();
  }
});

test("tag mismatch", async (t) => {
  const error = await t.throws(() => parseHTML("<div></div>"));
  console.log(error, "test");
  t.pass();
});
